body {
    background: url(./rivendell.jpg);
    background-position: center;
}

@media only screen and (max-width: 900px) {
    .container {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: 1fr 1fr;
        gap: 30px 0px;
        height: 100vh;
        justify-items: center;
        align-items: center;
    }
}

@media only screen and (min-width: 901px) {
    .container {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 1fr;
        gap: 0px 30px;
        height: 100vh;
        justify-items: center;
        align-items: center;
    }
}

.quote {
    font-weight: 500;
    height: 30vh;
    width: 30vw;
    min-width: 20rem;
    min-height: 15rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 3px solid white;
    background-color: rgba(100,100,100,0.5);
    text-align: center;
    padding: 0.5rem;
    color: white;
    opacity: 0.8;
}

