.answer {
    color: rgb(0,200,0);
}

.individualOptions {
    display: flex;
    justify-content: flex-end;
    padding: 0.2rem;
}

button {
    margin-top: 2rem;
    padding: 1rem;
    border-radius: 10px;
    color: white;
    background-color: rgba(0,0,0,0.8);
    border: none;
}

button:focus {
    outline: none;
}

button:hover {
    color: black;
    background-color: rgba(255,255,255,0.8);
}
